@import url('https://fonts.googleapis.com/css?family=Lato:400,700&display=swap');

.description {
  margin: 0 auto;
  margin-top: 50px;
  text-align: center;
  width: 250px;
  background-color: #fff;
  padding-bottom: 20px;
  border: 1px solid #d8e0ea;
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background-color: #e7ecf2;
}

.userAvatar {
  margin-top: 20px;
  border-radius: 50%;
}

.name {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #122236;
}

.tag {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #768696;
}

.location {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #768696;
}

.stats {
  padding: 0;
  margin: 0 auto;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 250px;
  text-align: center;
  background-color: #f3f6f9;
  margin-bottom: 50px;
}

.element {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 83px;
  padding: 12px 20px;
  border: 1px solid #d8e0ea;
}

.label {
  display: block;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: #768696;
}

.quantity {
  display: block;
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 14px;
  color: #122236;
}
